<template>
  <div id="pk-main-admin">
    <v-app-bar app>
      <v-app-bar-nav-icon to="/">
        <v-avatar color="blue" tile size="32">
          <span class="white--text">PK</span>
        </v-avatar>
      </v-app-bar-nav-icon>
      <v-toolbar-items>
        <v-btn dark depressed color="cyan" text>Admin Applikasi</v-btn>
      </v-toolbar-items>
      <v-spacer/>
      <v-btn text to="/admin/data">Data</v-btn>
      <v-btn text to="/admin/jalur">Jalur</v-btn>
      <v-btn text to="/app">Applikasi</v-btn>
      <v-btn text>Logout</v-btn>

      <v-progress-linear v-if="loading" indeterminate slot="extension" />
    </v-app-bar>
    <v-snackbar v-model="notif" :color="notifColor">
      {{ notifMessage }}
      <v-btn
        dark
        outlined
        @click="notif = false"
      >
        Close
      </v-btn>
    </v-snackbar>
    <router-view/>
  </div>
</template>

<script>
import Vue from "vue";
import evbus from "@/evbus";

export default {
  name: "AdminMainApp",
  data: () => ({
    menus: [
      { text: "RW", to: "/admin/rt" },
      { text: "RW", to: "/admin/rw" },
      { text: "Testing", to: "/admin/testing" },
      { text: "Applikasi", to: "/app" }
    ],
    notif: false,
    notifSuccess: false,
    notifMessage: '',

    loading: false,
  }),

  computed: {
    notifColor(){ return this.notifSuccess ? "green" : "pink darken-1" }
  },

  mounted() {
    evbus.$on("action-success", ({ msg }) => {
      console.log("HERE");
      this.notif = true;
      this.notifSuccess = true;
      this.notifMessage = msg;
    });
    evbus.$on("action-fail", ({ msg }) => {
      console.log("FAIL");
      this.notif = true;
      this.notifSuccess = false;
      this.notifMessage = msg;
    });
    evbus.$on("loading", (v) => {
      this.loading = v;
    });
  },

  beforeDestroy() {
    evbus.$off("app-success");
    evbus.$off("app-fail");
    evbus.$off("loading");
  }
}
</script>

